import * as React from "react";
import { Link } from "gatsby";
import { TECHNOLOGY } from "@data";

const TECHNOLOGY_STRUCTURE = [
    ["KEY_LARAVEL", "KEY_EXPRESSJS", "KEY_NODEJS"],
    ["KEY_VUE", "KEY_REACT"],
    ["KEY_ADONISJS", "KEY_GHOSTJS", "KEY_WORDPRESS"],
    [
        "KEY_PHP",
        "KEY_JAVASCRIPT",
        // 'KEY_PYTHON',
    ],
    ["KEY_MONGODB", "KEY_POSTGRESDB", "KEY_MYSQL"],
];

const TechnologyComponent = () => {
    const showSVG = (SVG = null) => {
        return SVG ? <SVG /> : null;
    };

    const TechnologyValues = TECHNOLOGY.value;

    return (
        <div className="content-section2 section-padding">
            <div className="container">
                <div className="row justify-content-center">
                    <div className="col-lg-9">
                        <div className="section-title text-center mb--50 mb-lg--80">
                            <h2 className="title">
                                {TECHNOLOGY.label}
                                {/* TechnologyComponent <br /> &amp; Expertise. */}
                            </h2>
                        </div>
                    </div>
                </div>

                {TECHNOLOGY_STRUCTURE.map((row, rowIndex) => (
                    <div className="row justify-content-center" key={rowIndex}>
                        {row.map((key, index) => (
                            <div
                                className="col-lg-4 col-md-6 mb--30"
                                key={index}
                            >
                                <div className="content-widget">
                                    <div
                                        className={`widget-icon ${TechnologyValues[key]["background"]}`}
                                    >
                                        {TechnologyValues[key]["svg"] ? (
                                            showSVG(
                                                TechnologyValues[key]["svg"]
                                            )
                                        ) : (
                                            <img
                                                src={
                                                    TechnologyValues[key][
                                                        "icon"
                                                    ]
                                                }
                                                alt={
                                                    TechnologyValues[key][
                                                        "label"
                                                    ]
                                                }
                                                loading="lazy"
                                            />
                                        )}
                                    </div>
                                    <div className="widget-text d-flex align-items-center">
                                        <h3 className="title">
                                            {TechnologyValues[key]["label"]}
                                        </h3>
                                    </div>
                                </div>
                            </div>
                        ))}
                    </div>
                ))}

                {/* <div className="h3 text-center">And many more...</div> */}
            </div>
        </div>
    );
};

export default TechnologyComponent;
